body {
  background: #FFF;
  font-size: 50px;
  font-size: calc(16px + (50 - 16) * ((100vw - 600px) / (1000 - 600)));
  line-height: 1.4;
}

@media (max-width: 600px) {
  body {
    font-size: 16px;
  }
}

@media (min-width: 1000px) {
  body {
    font-size: 50px;
  }
}

.block {
  display: flex;
}

body {
  font-family: 'Open Sans', sans;
}

.container {
  max-width: 1180px;
  margin: 0 auto;
}

@media only screen and (max-width: 1200px) {
  .container {
    margin: 0 20px;
  }
}

.hero {
  min-height: 100vh;
  width: 100%;
}

@media only screen and (max-width: 750px) {
  .hero {
    height: 100%;
    padding-bottom: 50px;
  }
}

.header {
  padding-top: 5px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: baseline;
}

@media only screen and (max-width: 550px) {
  .header {
    flex-direction: column;
  }
}

@media only screen and (max-width: 550px) {
  .logo {
    margin: 0 auto;
    margin-top: 20px;
  }
}

.logo img {
  height: 32px;
}

@media only screen and (max-width: 550px) {
  .logo img {
    margin-bottom: 20px;
  }
}

@media only screen and (max-width: 550px) {
  nav {
    margin: 0 auto;
  }
}

nav ul {
  position: relative;
  top: -3px;
}

nav ul li {
  display: inline-block;
}

@media only screen and (max-width: 550px) {
  nav ul li {
    display: block;
    text-align: center;
    padding-bottom: 15px;
  }
}

nav ul li a {
  font-size: 1rem;
  color: #d1d2d2;
  text-decoration: none;
  padding: 15px;
  position: relative;
}

nav ul li a:before {
  position: absolute;
  left: 15px;
  top: 15px;
  content: attr(data-content);
  display: inline-block;
  color: #fff;
  width: 100%;
  clip: rect(0, 0, 0, 0);
  transition: clip cubic-bezier(0.25, 0.46, 0.45, 0.94) 900ms;
}

@media only screen and (max-width: 550px) {
  nav ul li a:before {
    left: 0;
    padding-left: 0;
  }
}

nav ul li a:hover:before {
  clip: rect(0, 100px, 100px, 0);
}

.main {
  margin-top: 120px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}

@media only screen and (max-width: 901px) {
  .main {
    margin-top: 50px;
  }
}

.main__title {
  text-align: center;
  color: #FFF;
  font-size: 53px;
  font-weight: 300;
  line-height: 1.2;
  margin-bottom: 42px;
}

.main__subtitle {
  font-size: 18px;
  text-align: center;
  color: #FFF;
  font-weight: 300;
  margin-bottom: 25px;
}

.btn {
  position: relative;
  display: inline-block;
  color: #FFF;
  margin: 0 auto;
  margin-bottom: 30px;
  margin-top: 70px;
  font-size: 16px;
  text-decoration: none;
  line-height: 55px;
  padding: 0 30px;
  border-radius: 30px;
}

.btn-label {
  padding: 10px;
  display: inline-block;
  font-weight: bold;
}

.v19 .btn-label {
  padding: 10px 30px;
  font-family: arial;
  font-size: 14px;
  display: inline-block;
  z-index: 10;
  position: relative;
  white-space: nowrap;
  line-height: 24px;
  transition: all 0.225s;
  transition-delay: 0.45s;
  letter-spacing: 2px;
}

.v19.button-start .btn-label {
  padding: 12px 65px;
}

.v19 .btn-icon2 {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transition: all 0.225s;
  transform: skew(-40deg, 0deg) scaleX(1);
  background: rgba(240, 32, 47, 0.8);
  transition-delay: 0.45s;
}

.v19 .btn-icon {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transition: all 0.225s;
  transform: scale(0.9) skew(0deg, 0deg);
  transition-delay: 0.15s;
}

.v19 .btn-icon:before, .v19 .btn-icon:after {
  width: 60px;
  opacity: 0;
  content: '';
  height: 1px;
  position: absolute;
  display: block;
  background: #f0202f;
  transition: all 0.375s;
  transition-delay: 0.3s;
}

.v19 .btn-icon:before {
  top: 0;
  left: calc(50% - 30px);
}

.v19 .btn-icon:after {
  bottom: 0;
  right: calc(50% - 30px);
}

.v19 .btn-icon span:before, .v19 .btn-icon span:after {
  height: 0;
  transition-delay: 0s;
  content: '';
  background: #f0202f;
  position: absolute;
  display: block;
  width: 2px;
  transition: all 0.225s;
}

.v19 .btn-icon span:before {
  left: 0;
  top: 0;
}

.v19 .btn-icon span:after {
  right: 0;
  bottom: 0;
}

.v19:hover .btn-icon {
  transform: skew(-40deg, 0deg);
}

.v19:hover .btn-icon:before, .v19:hover .btn-icon:after {
  transition-delay: 0s;
  width: 30px;
  opacity: 1;
}

.v19:hover .btn-icon:before {
  left: 0;
}

.v19:hover .btn-icon:after {
  right: 0;
}

.v19:hover span:before, .v19:hover span:after {
  height: 30px;
  transition-delay: 0.45s;
}

.v19:hover .btn-icon2 {
  transform: skew(-40deg, 0deg) scaleX(0.2);
  background: transparent;
  transition-delay: 0s;
  opacity: 0;
}

.v19.white:hover .btn-label {
  color: #fff;
  transition-delay: 0s;
}

.v19.white .btn-icon2 {
  background: #fff;
}

.v19.white .btn-icon:before, .v19.white .btn-icon:after {
  background: #fff;
}

.v19.white span:before, .v19.white span:after {
  background: #fff;
}

.v19.labelred .btn-label {
  color: #f0202f;
}

.v19.labelgray .btn-label {
  color: #1D1D1D;
}

.v19.transparent {
  float: none;
  display: inline-block;
  -webkit-transform: translateZ(0px);
  transform: translateZ(0px);
}

.v19.transparent .btn-label {
  color: #fff;
  transition-delay: 0s;
}

.v19.transparent .btn-icon2 {
  background: transparent;
}

.v19.transparent .btn-icon {
  transform: scale(1) skew(10deg, 0deg);
}

.v19.transparent .btn-icon:before, .v19.transparent .btn-icon:after {
  background: #fff;
}

.v19.transparent span:before, .v19.transparent span:after {
  background: #fff;
}

.v19.transparent:hover .btn-icon {
  transform: scale(1) skew(-40deg, 0deg);
}

.features {
  border-top: 8px solid #34c482;
  padding-top: 75px;
  margin-bottom: 80px;
}

.features .container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

@media only screen and (max-width: 800px) {
  .features .container {
    flex-direction: column;
  }
}

.feature {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  margin-left: 30px;
}

.feature:first-of-type {
  margin-left: 0;
}

@media only screen and (max-width: 800px) {
  .feature {
    margin-left: 0;
  }
}

.feature img {
  margin: 0 auto;
  margin-bottom: 11px;
}

.feature__title {
  font-weight: 400;
  font-size: 20px;
  margin-bottom: 22px;
}

.feature__text {
  font-size: 15px;
  line-height: 1.5;
  text-align: center;
  font-size: 1.5rem;
  background: rgba(255, 255, 255, 0.38);
  font-family: 'Open Sans', sans-serif;
  color: #000;
  margin: 0 auto;
  padding: 5px 15px;
  box-shadow: 0 0 5px #000;
  z-index: 1;
}

@media only screen and (max-width: 800px) {
  .feature__text {
    margin-bottom: 50px;
  }
}
